<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal add-goal-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
            <div class="modal-content">
                <div class="window-title">{{$localizeFilter('add_goal_title')}}</div>
                <p class="window-text">{{$localizeFilter('add_goal_name_title')}}</p>
                <div class="input-field">
                    <input id="name" class="custom-input" :placeholder="$localizeFilter('add_goal_name')" type="text" v-model.trim="name"  :class="{ invalid: v$.name.$error }"/>
                    <small class="error" v-for="(error, index) of v$.name.$errors" :key="index">
                        {{ error.$message }}
                    </small>
                </div>

                <p class="window-text">{{$localizeFilter('add_goal_amount_title')}}</p>
                <div class="input-field">
                    <input id="amount" class="custom-input" :placeholder="$localizeFilter('add_goal_amount')" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
                    <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                        {{ error.$message }}
                    </small>
                </div>
                
                <p class="window-text">{{$localizeFilter('add_goal_percent_title')}}</p>
                <div class="input-field">
                    <input id="percentOfDeductions" class="custom-input" :placeholder="$localizeFilter('add_goal_percent')" type="text" v-model.trim="percentOfDeductions"  :class="{ invalid: v$.percentOfDeductions.$error }"/>
                    <small class="error" v-for="(error, index) of v$.percentOfDeductions.$errors" :key="index">
                        {{ error.$message }}
                    </small>
                </div>

                <button class="basic-button green-button" @click="submitHandler">{{$localizeFilter('confirm')}}</button>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../../filters/localize.filter'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric } from "@vuelidate/validators";

export default defineComponent({
  props: {
  },
  setup() {
    return { v$: useVuelidate() };
  },
  validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), numeric)
            },
            name: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required)
            },
            percentOfDeductions: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), numeric)
            },
        };
    },
  data: ()=>({
    percentOfDeductions:null,
    amount:null,
    name:null
  }),
  computed: {
  },
  methods: {
      closeModal(reload) {
          this.$emit('closeModal', reload)
      },
      async submitHandler(){
        this.v$.$touch()
        if (this.v$.$error) { return }
        try {
            const data = {name: this.name, amount: this.amount, percentOfDeductions: this.percentOfDeductions}
            this.result = await this.$store.dispatch('addGoal', data)
            this.$emit('addGoalSuccesfull')
            this.$message(localizeFilter('your_application_sent_successful'))
        } catch(e) {
            this.$error(e)
        }
      }
      }
})
</script>


