                    {{`${goal.percentOfDeductions}%`}}
                </div>
            </div>

            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('time_of_completion')}}
                </div>
                <div class="text bold">
                    {{`~${getTimeString(timeOfCompletion)}`}}
                </div>
            </div>

            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('amount')}}
                </div>
                <div class="text bold">
                    {{`${goal.amount / 1000000} USDT`}}
                </div>
            </div>
        </div>
      </div>

      <div class="second-row">
        <div class="second-row-left">
        <div :class="`amount-block`">
            <div class="amount-text">{{$localizeFilter('accumulated')}}:</div>
            <img class="amount-image" src="@/assets/newImages/coins/usdt-trc20.png">
            <div class="amount">{{`${$utils.formatNumber((countAccumulated / 1000000), 'en-US', 6, 6)} USDT`}}</div>
        </div>

        <div class="withdraw-block">
          <!-- v-if="goal.availableProfitToWithdraw > 0" -->
            <div class="withdraw-left-block">
                <div class="available-to-withdraw">{{$localizeFilter('available_to_withdraw', [`${$utils.formatNumber(goal.availableProfitToWithdraw, 'en-US', 6, 6)} USDT`])}}</div>
            </div>
        </div>

        </div>
        <div class="second-row-right">

        <div class="progress-block">
          <div class="progress-title">
            {{$localizeFilter('left_to_goal', [`${$utils.formatNumber((countLeft / 1000000), 'en-US', 6, 6)} USDT`])}}
          </div>
          <div class="progress-labels-block">
            <label class="progress-left-label">0%</label>
            <label class="progress-right-label">100%</label>
          </div>
          <div class="progress">
            <div class="progress-background" :style="`width: ${progress*maxProgressWidth}px;`"></div>
          </div>
        </div>

        <div class="text-block" :style="'align-items: center;'">
                <div class="buttons-block">
                    <div class="basic-button" @click="withdraw">{{$localizeFilter('operation_withdraw')}}</div>
                </div>
                <div class="comission">{{$localizeFilter('comission_1_usdt')}}</div>
            </div> 
        </div>  

        </div>
      
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../filters/localize.filter'
import anime from 'animejs/lib/anime.es.js';

export default defineComponent({
    props: {
        goal: {
            type: Object,
            required: true
        }
    },
    setup() {
        
    },
    data: ()=>({
        maxProgressWidth: 266,
        countAccumulated: 0,
        countLeft: 0,
        index: 0
    }),
    watch: { 
        goal(newVal, oldVal) {
            if ((newVal.accumulated !== oldVal.accumulated)) {
              this.$nextTick(()=>{
                      // if (this.index>0) {
                          this.countAccumulated = oldVal.accumulated
                          this.countLeft = this.amountLeft(oldVal.amount, oldVal.accumulated)
                          this.setAccumulated(newVal.accumulated)
                          this.setLeft(this.amountLeft(newVal.amount, newVal.accumulated))
                      // } else {
                      //     this.countAccumulated = newVal.accumulated
                      //     this.countLeft = this.amountLeft(newVal.amount, newVal.accumulated)
                      //     this.index=1
                      // }
                })
            }
        }
    },
    computed: {
      progress() {
        return this.goal.accumulated / this.goal.amount
      },
      timeOfCompletion() {
        const currentDate = +Date.now()
        const createdTimestamp = this.goal.createdTimestamp
        const progress = this.progress
        // console.log(progress, createdTimestamp, currentDate)
        // return createdTimestamp + (currentDate - createdTimestamp)/progress - currentDate
        return Math.max(0, (currentDate - createdTimestamp)/progress * (1 - progress))
      }
    },
    methods: {
        setAccumulated(val) {
          const obj = { n: this.countAccumulated }
          anime({
              targets: obj,
              n: val,
              round: 1,
              duration: 3000,
              easing: 'linear',
              update: () => {
              this.countAccumulated = obj.n
              }
          })
        },
        setLeft(val) {
          const obj = { n: this.countLeft }
          anime({
              targets: obj,
              n: val,
              round: 1,
              duration: 3000,
              easing: 'linear',
              update: () => {
              this.countLeft = obj.n
              }
          })
        },
        withdraw() {
            this.$emit('withdraw', this.goal)
        },
        getTimeString(miliseconds) {
            if (miliseconds / 3600000 >= 1) {
                const hours = Math.ceil(miliseconds / 3600000)
                return localizeFilter('hours_short', [hours])
            } else {
                const minutes = Math.ceil(miliseconds / 60000)
                return localizeFilter('minutes_short', [minutes])
            }
        },
        amountLeft(amount, accumulated) {
            return Math.max(0, amount - accumulated)
        }
    },
    mounted() {
      this.countAccumulated = this.goal.accumulated
      this.countLeft = this.amountLeft(this.goal.amount, this.goal.accumulated)
    }
})
</script>

